.page-header {
  position: relative;
  width: 100%;
  min-height: 53px;
  border-bottom: 1px solid #C0C0C0;
}

.page-header__logo img {
  position: absolute;
  top: 7px;
  left: 44%;
  width: 44px;
  height: 35px;
  z-index: 1;
}

.page-header__search form {
  display: none;
}

.page-header__search,
.page-header__mobile img {
  position: absolute;
  top: 3px;
  left: 12%;
  z-index: 1;
}

.page-header__favorites img {
  position: absolute;
  top: 6px;
  left: 79%;
  z-index: 1;
}

.page-header__favorites span {
  position: absolute;
  z-index: 1;
  top: 26px;
  left: 78.5%;
  color: white;
  font-size: 10px;
  text-align: center;
  border-radius: 9px;
  height: 14px;
  width: 14px;
  background-color: #ff0075;
}

.page-header__basket img {
  position: absolute;
  top: 6px;
  left: 89%;
  z-index: 1;
}

.page-header__basket span {
  position: absolute;
  z-index: 1;
  top: 26px;
  left: 88.5%;
  color: white;
  font-size: 10px;
  text-align: center;
  border-radius: 9px;
  height: 14px;
  width: 14px;
  background-color: #ff0075;
}

.page-header__ticker {
  display: none;
}

.page-header__btn-menu {
  padding: 5px;
  position: absolute;
  top: 8px;
  left: 12px;
  cursor: pointer;
  transition: right .23s;
  z-index: 3;
  width: 20px;
  transform: translateZ(0);
  backface-visibility: hidden;
}

.page-header__btn-menu span {
  display: block;
  height: 0.7pt;
  background-color: #444;
  margin: 5px 0 0;
  transition: all .1s linear .23s;
  position: relative;
}

.page-header__ticker:checked ~ .page-header__btn-menu {
  left: 325px;
  top: 2px;
  width: 16px;
}

.page-header__ticker:checked ~ .main-nav {
  left: 0;
}

.page-header__ticker:checked ~ .page-header__btn-menu span.page-header__first {
  transform: rotate(45deg);
}

.page-header__ticker:checked ~ .page-header__btn-menu span.page-header__second {
  display: none;
}

.page-header__ticker:checked ~ .page-header__btn-menu span.page-header__third {
  transform: rotate(-45deg);
  top: -6px;
}

.page-header__search-button {
  display: none;
}

.page-header__user {
  display: none;
}

@media (min-width: $desktop-width) {
  .page-header {
    border-bottom: none;
  }

  .page-header__wrapper {
    position: relative;
    max-width: 93.5rem;
    margin: 0 auto;
  }

  .page-header__btn-menu {
    display: none;
  }

  .page-header__logo img {
    top: 50px;
    left: 48%;
    width: 64px;
    height: 48px;
  }

  .page-header__search form {
    display: block;
  }

  .page-header__mobile img {
    display: none;
  }

  .page-header__search {
    top: 4px;
    left: 4%;
  }

  .page-header__search-window {
    display: block;
    width: 160px;
    height: 25px;
    padding-left: 40px;
    border: 2px solid white;
    background-color: #f2f2f2;
    border-radius: 25px;
    outline: none;
    transform: rotate(0.7);
    transition: all .1s linear .23s;
  }

  .page-header__search-window:focus {
    background-color: white;
    border: 2px solid #ff0075;
    width: 300px;
    transform: rotate(1.2);
    transition: all .1s linear .23s;
  }

  .page-header__button {
    position: relative;
    display: contents;
    outline: none;
  }

  .page-header__button img {
    position: absolute;
    top: -7px;
    left: 0;
    outline: none;
  }

  .page-header__favorites img {
    top: 70px;
    left: 93%;
  }

  .page-header__favorites span {
    top: 90px;
    left: 92.6%;
  }

  .page-header__basket img {
    top: 70px;
    left: 96%;
  }

  .page-header__basket span {
    top: 90px;
    left: 95.6%;
  }

  .page-header__user {
    display: block;
  }

  .page-header__user img {
    position: absolute;
    top: 70px;
    left: 90%;
    z-index: 1;
  }
}
