.modal-search {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 90rem;
  margin: 0 auto;
}

#modal {
  display: none;
}

#modal:target {
  display: block;
}

.modal-search__main {
  display: flex;
  margin-bottom: 20px;
}

.modal-search__container {
  position: absolute;
  top: -120px;
  left: 43px;
  background-color: white;
  width: 39rem;
  box-shadow: 0 1px 4px 1px #f2f2f2;
}

.modal-search__title {
  text-transform: uppercase;
  font-size: 12px;
  color: #ff0075;
}

.modal-search__list {
  margin: 0;
  padding: 0;

  &--selection {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 550px;
  }
}

.modal-search__item {
  margin: 0;
  padding: 0;
  list-style: none;

  &--selection {
    text-align: center;
  }
}

.modal-search__item a {
  text-decoration: none;
  color: black;
}

.modal-search__item a:hover {
  color: #ff0075;
  border-bottom: 1px solid #ff0075;
}

.modal-search__item--selection a:hover {
  color: #707070;
  border-bottom: none;
}

.modal-search__wrapper {
  margin-left: 40px;
  margin-right: 30px;
}

.modal-search__image {
  width: 100px;
}

.modal-search__item-title {
  margin: 0;
  margin-top: 10px;
  margin-bottom: 8px;
  text-transform: uppercase;
  font-size: 10px;
}

.modal-search__description {
  margin: 0;
  font-style: italic;
  font-size: 11px;
}

.modal-search__price {
  margin: 0;
  margin-top: 5px;
}
